
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f9f9f9;
}

.container {
  max-width: 900px;
  margin: auto;
  padding: 20px;
  background-color: white;
  border: 1px solid #ccc;
  margin-top: 20px;
}

.header p {
  margin: 5px 0;
}

.section {
  margin-top: 30px;
  padding-top: 10px;
  border-top: 1px solid #ddd;
}

.section h2 {
  margin-bottom: 10px;
}

input[type="text"] {
  margin: 5px;
  padding: 5px;
  width: 200px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

button {
  margin: 5px;
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

button:disabled {
  background-color: #ccc;
  color: white;
  cursor: not-allowed;
}

button:not(:disabled):hover {
  opacity: 0.9;
}

button.create {
  background-color: #007bff;
  color: white;
}

button.bid {
  background-color: #28a745;
  color: white;
}

button.cancel {
  background-color: #dc3545;
  color: white;
}

button.fulfill {
  background-color: #ffc107;
  color: black;
}

button.claim {
  background-color: #6c757d;
  color: white;
}

button.withdraw,
button.owner,
button.destroy {
  background-color: #007bff;
  color: white;
}

.auction-box {
  padding: 10px;
  border: 1px solid #ddd;
  margin-bottom: 10px;
  background-color: #fafafa;
}

.message {
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  text-align: center;
  font-weight: bold;
}

.message.success {
  background-color: #d4edda;
  color: #155724;
}

.message.error {
  background-color: #f8d7da;
  color: #721c24;
}

.btn-green {
  background-color: #4CAF50;
  color: white;
}

.btn-red {
  background-color: #f44336;
  color: white;
}

.btn-blue {
  background-color: #2196F3;
  color: white;
}

.auction-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
}

.auction-table th,
.auction-table td {
  border: 1px solid #ccc;
  padding: 8px;
  text-align: center;
}

.auction-table th {
  background-color: #f2f2f2;
}
